import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { PagesComponent } from '@pages';
import { URL_PATHS } from '@shared';


const routes: Routes = [
  {
    path: '',
    component: PagesComponent,
    children: [
      {
        path: URL_PATHS.APPLICATION,
        loadChildren: () => import('./applications/applications.module')
          .then(m => m.ApplicationsModule)
      },
      {
        path: URL_PATHS.COMPANY,
        loadChildren: () => import('./company/company.module')
          .then(m => m.CompanyModule)
      },
      {
        path: URL_PATHS.DASHBOARD,
        loadChildren: () => import('./dashboard/dashboard.module')
          .then(m => m.DashboardModule)
      },
      {
        path: URL_PATHS.MAINTENANCE,
        loadChildren: () => import('./maintenance/maintenance.module')
          .then(m => m.MaintenanceModule)
      },
      {
        path: URL_PATHS.ACCOUNT,
        loadChildren: () => import('./account/account.module')
          .then(m => m.AccountModule)
      },
      {
        path: URL_PATHS.ANNOUNCEMENT,
        loadChildren: () => import('./announcement/announcement.module')
          .then(m => m.AnnouncementModule)
      },
      {
        path: URL_PATHS.ACCOUNTING,
        loadChildren: () => import('./accounting/accounting.module')
          .then(m => m.AccountingModule)
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PagesRoutingModule { }
